@font-face {
  font-family: "Oswald";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../source/fonts/oswaldregular.woff") format("woff");
  src: url("../source/fonts/oswaldregular.woff2") format("woff2"); }

@font-face {
  font-family: "Montserrat";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../source/fonts/Montserrat-Regular.woff") format("woff"); }

body {
  margin: 0;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", Arial, sans-serif;
  color: #30135a;
  font-style: normal;
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0); }

.title {
  margin: 0;
  font-size: 36px;
  line-height: 24px;
  font-family: "Oswald", Arial, sans-serif;
  font-weight: 400;
  color: #30135a;
  font-style: normal;
  text-transform: uppercase; }
  @media screen and (max-width: 960px) {
    .title {
      font-size: 28px; } }

