.nav_header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  background-color: #311258; }

.nav-wrapper {
  display: flex;
  align-items: center;
  max-width: 1170px;
  margin: 0 auto;
  justify-content: center; }

.nav {
  font-size: 20px;
  font-family: "Oswald", Arial, sans-serif;
  line-height: 24px;
  text-transform: uppercase;
  margin: 0 auto; }

.nav_input--mobile {
  display: none; }
  @media screen and (max-width: 960px) {
    .nav_input--mobile:checked ~ .nav_list {
      left: 0; }
    .nav_input--mobile:checked ~ label .nav_img--mobile {
      background: url("../source/img/close.svg"); } }

.nav_img--mobile {
  display: none; }
  @media screen and (max-width: 960px) {
    .nav_img--mobile {
      display: block;
      background: url("../source/img/hamburger.svg");
      background-repeat: no-repeat;
      background-size: 100%;
      width: 36px;
      height: 36px;
      background-position: center, center;
      position: absolute;
      top: 0;
      right: 15px;
      transform: translate(-100%, 60%);
      cursor: pointer;
      z-index: 99; } }

.nav_list {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 36px;
  padding: 0;
  list-style: none;
  margin: 0 auto; }
  @media screen and (max-width: 960px) {
    .nav_list {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100vh;
      position: absolute;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
      background: #fff; } }

.nav_item a {
  display: block;
  color: #fff;
  text-decoration: none;
  padding: 24px 0;
  border: 2px solid transparent; }
  .nav_item a:hover {
    border-bottom: 2px solid #d736bc;
    box-shadow: 0 6px 6px -6px #d736bc;
    transition: 0.3s; }
  @media screen and (max-width: 960px) {
    .nav_item a {
      color: #30135a; } }

@media screen and (max-width: 960px) {
  .nav_social-list {
    display: none; } }

.home {
  max-width: 1170px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 96px;
  margin: 0 auto;
  padding: 10rem 0; }
  @media screen and (max-width: 960px) {
    .home {
      grid-template-columns: 1fr;
      padding: 0 30px;
      gap: 76px; } }
  @media screen and (max-width: 480px) {
    .home {
      grid-template-columns: 1fr;
      padding: 0 20px;
      gap: 36px; } }

.home_img {
  height: 600px;
  width: 600px;
  margin: auto auto;
  background: url("../source/img/bg.svg"), url("../source/img/small_photo2.png");
  background-repeat: no-repeat, no-repeat;
  background-size: 100%, 50%;
  background-position: center, center; }
  @media screen and (max-width: 960px) {
    .home_img {
      height: 450px;
      width: 450px; } }
  @media screen and (max-width: 480px) {
    .home_img {
      height: 350px;
      width: 350px; } }

.home_text {
  margin: auto 0; }

.home_title {
  margin-bottom: 44px; }

.home_description {
  margin-bottom: 32px; }

.socials-list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;
  list-style: none;
  margin: 0;
  padding: 0; }

.socials-item {
  width: 32px;
  height: 32px;
  cursor: pointer;
  background-position: 50% 50%; }

.social-vk {
  background: no-repeat url("../source/img/vk.svg"); }

.social-twitter {
  background: no-repeat url("../source/img/twitter.svg"); }

.social-linkedin {
  background: no-repeat url("../source/img/linkedin.svg"); }

.contacts {
  max-width: 1170px;
  margin: 0 auto;
  display: grid;
  grid-template-rows: 30% auto;
  padding: 0 30px;
  padding-bottom: 10rem; }

.contacts_title {
  text-align: center;
  margin-bottom: 44px; }

.contacts_description {
  margin: 0 auto;
  max-width: 400px; }
  @media screen and (max-width: 960px) {
    .contacts_description {
      max-width: 300px; } }

.contacts_form {
  min-width: 500px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media screen and (max-width: 960px) {
    .contacts_form {
      min-width: 300px; } }

.contacts_text-field {
  padding: 20px 24px;
  margin: 20px 0;
  width: 100%;
  box-sizing: border-box;
  height: 30px;
  border: 2px solid #311258;
  border-radius: 50px;
  font-family: "Montserrat";
  color: #30135a;
  font-size: 16px; }
  .contacts_text-field::placeholder {
    color: #30135a;
    opacity: 0.7; }

.contacts_text-field--textarea {
  min-height: 200px;
  border-radius: 16px; }

.contacts_button {
  width: 100%; }

.footer {
  background-color: #311258;
  color: #fff; }

.footer_wrapper {
  max-width: 1170px;
  margin: 0 auto; }

.footer_social-list {
  padding-top: 68px;
  margin-bottom: 32px; }

.footer_social-list .socials-item {
  width: 60px;
  height: 60px; }

.footer-resume-wrapper {
  text-align: center;
  padding-bottom: 32px; }

.footer_resume,
.footer_link {
  display: inline-block;
  font-size: 32px;
  line-height: 24px;
  font-family: "Oswald", Arial, sans-serif;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase; }

.footer_link {
  padding-bottom: 4px;
  color: #d736bc;
  text-decoration: none; }
  .footer_link:hover {
    border-bottom: 2px solid #d736bc;
    box-shadow: 0 6px 6px -6px #d736bc; }

.slider {
  max-width: 1170px;
  display: grid;
  grid-template-rows: 100px 1fr;
  margin: 0 auto;
  padding: 0 30px;
  margin-bottom: 52px;
  padding-bottom: 10rem; }

.slider_title {
  margin: auto;
  margin-bottom: 44px; }

.slider_wrapper {
  display: grid;
  grid-template-columns: 90px 1fr 90px;
  column-gap: 60px;
  margin: auto; }
  @media screen and (max-width: 960px) {
    .slider_wrapper {
      grid-template-columns: 1fr; } }

.slider_button {
  position: relative;
  height: 106px;
  margin: auto;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer; }
  @media screen and (max-width: 960px) {
    .slider_button {
      display: none; } }

.slider_button--left:hover {
  animation: rightShift 0.4s linear; }

@keyframes rightShift {
  0% {
    right: 0; }
  50% {
    right: 5%; }
  100% {
    right: 0%; } }

.slider_button--right:hover {
  animation: leftShift 0.4s linear; }

@keyframes leftShift {
  0% {
    left: 0; }
  50% {
    left: 5%; }
  100% {
    left: 0%; } }

.slider_container {
  height: auto;
  width: 600px; }
  .slider_container:hover {
    transform: scale(1.03);
    transition: all 0.2s ease-out;
    box-shadow: -15px 15px 30px -20px #d736bc; }
  @media screen and (max-width: 960px) {
    .slider_container {
      width: 400px; }
      .slider_container:hover {
        transform: none;
        box-shadow: none; } }

.slider_item {
  line-height: 0; }

.skills {
  max-width: 1170px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 10% 1fr;
  column-gap: 96px;
  row-gap: 44px;
  padding: 0 30px;
  margin: 0 auto;
  padding-bottom: 10rem; }
  @media screen and (max-width: 960px) {
    .skills {
      grid-template-columns: 1fr;
      grid-template-rows: 10% 15% repeat(5, 15%);
      gap: 15px; } }
  @media screen and (max-width: 480px) {
    .skills {
      grid-template-columns: 1fr;
      padding: 0 20px;
      gap: 36px; } }

.skills_title {
  grid-column: 1/-1;
  margin: 0 auto; }

.skills_list {
  padding: 0;
  display: grid;
  grid-template-columns: 20% 1fr;
  grid-template-rows: repeat(5, 70px); }

.skill_text {
  margin: 0;
  font-size: 28px;
  line-height: 24px;
  font-family: "Oswald", Arial, sans-serif;
  font-weight: 400;
  color: #30135a;
  font-style: normal;
  padding-right: 20px; }

.skills_level {
  padding: 0;
  margin: 0;
  height: 30px;
  font-size: 0;
  border: 2px solid #311258;
  border-radius: 50px; }

.skill_percents {
  height: 30px;
  background-color: #311258;
  border-radius: 50px; }

.arrow {
  position: absolute;
  top: 80%;
  left: 50%; }
  .arrow:hover {
    animation: Floating 0.6s linear; }
  @media screen and (max-width: 960px) {
    .arrow {
      display: none; } }

@keyframes Floating {
  0% {
    top: 80%; }
  50% {
    top: 83%; }
  100% {
    top: 80%; } }

.button {
  padding: 20px 36px;
  border: none;
  border-color: #311258;
  border-radius: 16px;
  background-color: #311258;
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  font-size: 20px; }
  .button:hover {
    transform: scale(1.03);
    transition: all 0.3s ease-out; }
  @media screen and (max-width: 960px) {
    .button {
      width: 90%; }
      .button:hover {
        transform: none; } }
