.arrow {
  position: absolute;

  top: 80%;
  left: 50%;

  &:hover {
    animation: Floating 0.6s linear;
  }

  @media screen and (max-width: 960px) {
    display: none;
  }
}

@keyframes Floating {
  0% {
    top: 80%;
  }

  50% {
    top: 83%;
  }

  100% {
    top: 80%;
  }
}
